body,
html,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
fieldset,
figure,
pre {
  margin: 0;
  padding: 0; }

ul,
ol {
  list-style: none; }

a,
button {
  font: inherit;
  color: inherit;
  border: none;
  text-decoration: none; }
  a:focus,
  button:focus {
    outline: none; }

fieldset,
hr {
  border: none; }

input,
textarea,
select {
  font: inherit;
  color: inherit;
  border: none;
  box-shadow: none; }
  input::placeholder,
  textarea::placeholder,
  select::placeholder {
    opacity: 0.5;
    color: inherit; }
  input:focus,
  textarea:focus,
  select:focus {
    outline: none; }

textarea {
  resize: none;
  vertical-align: top; }

*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

a {
  transition: color 0.3s, border-color 0.3s, background-color 0.3s, text-decoration 0.3s, box-shadow 0.3s, opacity 0.3s;
  color: inherit; }
  a:hover {
    opacity: 0.7; }

img {
  max-width: 100%;
  height: auto;
  border-radius: 10px; }

button {
  cursor: pointer; }

body {
  position: relative;
  min-width: 320px;
  max-width: 3000px;
  margin: 0 auto;
  color: white;
  background-color: #0099ff;
  background-image: linear-gradient(to bottom left, #ff78ee 0%, #0099ff 66%, #5db3ff 100%); }

.img-container {
  line-height: 0; }

.hidden {
  display: none; }

h1 {
  margin-bottom: 0.4em; }

h2 {
  margin-bottom: 1.1em; }

p:not(:last-child) {
  margin-bottom: 1em; }

body {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 20px;
  line-height: 1.33;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }
  @media (min-width: 767px) {
    body {
      font-size: 30px; } }

a {
  font-size: inherit; }

h1 {
  font-size: 30px;
  font-weight: normal; }
  @media (min-width: 479px) {
    h1 {
      font-size: 33px; } }
  @media (min-width: 767px) {
    h1 {
      font-size: 60px; } }
  @media (min-width: 992px) {
    h1 {
      font-size: 33px; } }
  @media (min-width: 1280px) {
    h1 {
      font-size: 60px; } }

h2 {
  font-size: 30px;
  line-height: 1.3;
  font-weight: 600; }
  @media (min-width: 479px) {
    h2 {
      font-size: 33px; } }

h3 {
  font-size: 30px; }

h4 {
  font-size: 25px; }

h5 {
  font-size: 20px; }

h6 {
  font-size: 18px; }

footer,
header {
  line-height: 1.7; }

.v-animate-before {
  opacity: 0;
  transform: translate3d(0, 2em, 0); }

.v-animate-enter {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  transition: opacity 1s, transform 1s; }

.animate {
  animation-name: slideUp, fadeIn;
  animation-duration: 1s;
  animation-fill-mode: both; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes slideUp {
  from {
    transform: translate3d(0, 2em, 0); }
  to {
    transform: translate3d(0, 0, 0); } }

@keyframes slideFromRight {
  from {
    transform: translate3d(100%, 0, 0); }
  to {
    transform: translate3d(0, 0, 0); } }

.btn,
button {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  background-color: transparent;
  box-shadow: none;
  cursor: pointer;
  color: white;
  padding: 0.4em 2.4em;
  background-color: #0099ff;
  border-radius: 2em;
  box-shadow: 0 18px 35px rgba(0, 153, 255, 0.1);
  background-color: white;
  color: #0099ff;
  transition: all 0.3s;
  font-size: 20px; }
  .btn:hover,
  button:hover {
    box-shadow: 0 6px 10px rgba(0, 153, 255, 0.1);
    opacity: 0.9; }
  .btn:focus,
  button:focus {
    box-shadow: 0 0 1em rgba(0, 153, 255, 0.1);
    opacity: 0.7; }

form {
  max-width: 1200px; }

.form-block + .form-block,
.form-block + .form-block-double {
  margin-top: 2em; }

.form-block + .form-block,
.form-block + .form-block-double {
  margin-top: 2em; }

.form-block-double + .form-block,
.form-block-double + .form-block-double {
  margin-top: 2em; }

@media (min-width: 992px) {
  .form-block-double {
    display: flex;
    justify-content: space-between; }
    .form-block-double .form-block {
      margin: 0;
      width: 46%; } }

input,
textarea,
select {
  width: 100%;
  background-color: transparent;
  padding: 0.7em 0.1em;
  transition: all 0.3s;
  border-bottom: 2px solid white; }
  input:disabled, input:read-only,
  textarea:disabled,
  textarea:read-only,
  select:disabled,
  select:read-only {
    pointer-events: none; }
  input:focus,
  textarea:focus,
  select:focus {
    border-bottom-color: #99d6ff; }
