
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      
























































.contact-form {
  & > .form-block {
    @for $i from 1 through 4 {
      &:nth-child(#{$i}) {
        animation-delay: $i * 0.4s;
      }
    }
  }
  .form-block-double {
    .form-block {
      &:last-child {
        animation-delay: 0.4s;
      }
    }
  }
}
