
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      



























































.skills {
  @include respond-to(large) {
    margin-bottom: 10vh;
  }
}
.skill-list {
  text-align: center;
  @include respond-to(medium) {
    display: flex;

    .skill-list-item {
      margin-bottom: 0;
    }
  }
  @include respond-to(large) {
    text-align: left;

    .skill-list-item {
      margin-left: 0;
      margin-right: 4em;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  margin-bottom: 2em;
}
.skill-list-item {
  position: relative;
  .skill-value {
    margin-bottom: 0;
  }

  @for $i from 0 through 4 {
    &:nth-child(#{$i + 1}) {
      animation-delay: $i * 0.4s;
    }
  }
  @include respond-to(x-small) {
    margin: 0 auto 1em;
    width: 400px;
    height: 400px;
    border-radius: 50%;
    background-image: linear-gradient(215deg, transparent 20%, rgba(white, 0.2));
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @include respond-to(medium) {
    width: 300px;
    height: 300px;
  }
  @include respond-to(large) {
    white-space: nowrap;
    justify-content: flex-start;
    padding-left: 3em;
  }
}
.skill-value {
  font-size: $font-size-xxx-large;
  opacity: 0.5;

  @include respond-to(x-small) {
    font-size: $font-size-xxxx-large;
  }
}
.skill-value-percent {
  font-size: $font-size-xx-large;

  @include respond-to(x-small) {
    font-size: $font-size-xxx-large;
  }
}
.skill-title {
  font-weight: 400;
  letter-spacing: 2px;

  @include respond-to(medium) {
    font-size: $font-size-large;
  }
  @include respond-to(large) {
    font-size: $font-size-xx-large;
  }
}
.skills-description {
  max-width: 1030px;
  animation-delay: 1.2s;
}
